
.home > {
  .wbw-navbar {
    transition: none;
  }

  .wbw-navbar-solid {
    > a.nav-link, a.nav-link {
      color: #222;
    }
  }
}
.home > .wbw-navbar-solid {
	background: #f8f9fa;
	opacity: 0.9;
	@include breakpoint (xs) {
		opacity: 1;
	}
}
.wbw-navbar-solid {
	background: #f8f9fa;
}
 .wbw-navbar-solid {
    > a.nav-link, a.nav-link {
      color: #222;
    }
  }
a.nav-link {
  color: #fff;
  font-size: 1rem;
  text-transform: uppercase;
  position: relative;
  border-bottom: 2px solid transparent;
  margin-right: 1.1rem;
  @include breakpoint (xs) {
  	font-size: 1em;
  	margin-left: 15px;
  	margin-bottom: 15px;
  	color: #000;
  	width: 45%;
  }
  @include breakpoint (sm) {
  	font-size: 1.1rem;
  	margin-left: 15px;
  }
  @include breakpoint (md) {
  	font-size: 0.76rem;
  }
  @include breakpoint (lg) {
  	font-size: 1rem;
  }
  &:hover {
  	color: #b4a269ff;
  	border-bottom: 2px solid #b4a269ff;
  }
}
.navbar-toggler {
    background-color: #b4a269ff;
    border: 0px solid transparent;
    transition: 0.4s ease-in-out;
    &:hover {
    	background-color: #ceb877;
    }
    &:focus {
    	outline: none;
    }
}
.navbar-collapse.collapsing,
.navbar-collapse.show {
    background: #f8f9fa;
    margin-left: -15px;
    margin-right: -15px;
    @include breakpoint (sm) {
    	background: transparent;
    }
}
.embed-responsive .embed-responsive-item, 
.embed-responsive embed, 
.embed-responsive iframe, 
.embed-responsive object, 
.embed-responsive video {
    height: 101%;
}
#wbwFrame.embed-responsive {
	overflow: visible;
	@include breakpoint (md) {
		overflow: hidden;
	}
}
.bg-beer-card {
	background: #b4a269ff;
}
/* WBW COLOR CODED BUTTONS AND ALERTS */
.btn-blue {
	color: #b4a269ff;
	background-color: #1d547e;
	border-color: #222;
	&:hover {
		background-color: #143b59;
		color: #b4a269ff;
	}
}
